<template>
  <v-container fluid class="search">
    <sidemenu></sidemenu>
    <h2 class="title">Editar mis datos</h2>
    <div class="search-ctn container">
      <v-text-field
        v-model="name"
        label="Nombres"
        required
      ></v-text-field>
      <v-text-field
        v-model="lastname"
        label="Apellidos"
        required
      ></v-text-field>
      <v-text-field
        v-model="dni"
        :mask="'########'"
        label="Número de DNI"
        required
      ></v-text-field>
      <v-text-field
        v-model="phone"
        :mask="'### ### ###'"
        label="Número de Celular"
        required
      ></v-text-field>
      <v-flex xs12 sm12 text-center style="margin-bottom:10px;">
          <v-btn
              style="color:#fff"
              color="teal"
              @click="saveData"
              :disabled="!name || !phone || !lastname || !dni"
          >
            Guardar
          </v-btn>
      </v-flex>
    </div>
  </v-container>
</template>

<script>
import sidemenu from '../components/Sidemenu'
import { mapActions } from 'vuex'
export default {
  name: 'Config',
  data () {
    return {
      name: '',
      lastname: '',
      phone: '',
      dni: ''
    }
  },
  mounted () {
    this.getMyData()
  },
  methods: {
    getMyData () {
      this.setLoader(true)
      this.getMyInfo().then((data) => {
        this.name = data.name
        this.lastname = data.lastName
        this.phone = data.phone
        this.dni = data.identity
        this.setLoader(false)
      }).catch(() => {
        this.setLoader(false)
      })
    },
    saveData () {
      this.setLoader(true)
      let data = {
        name: this.name,
        lastName: this.lastname,
        identity: this.dni,
        identityType: 'DNI',
        phone: this.phone
      }
      this.setMyInfo(data).then((data) => {
        // close loader
        this.setLoader(false)
      }).catch(() => {
        // close loader
        this.setLoader(false)
      })
    },
    ...mapActions([
      'getMyInfo',
      'setMyInfo',
      'setLoader'
    ])
  },
  components: {
    sidemenu
  }
}
</script>

<style lang="stylus" scoped>
.search
  padding 0
  .title
    padding-top 20px
    text-align center
    color #2196F3
    margin-bottom 20px
  .search-ctn
    padding 12px
.divisor
  margin 20px 0
.chefs-row
  width 100%
  >>> .v-card
    padding: 8px
    margin-bottom: 10px
  >>> .v-divider
    margin 10px 0 0 0
</style>
