import Service from './Service'

class MapService extends Service {
  getMarkers (data) {
    return this.request({
      url: 'https://api.donalolafood.com/api/chef/nearby',
      method: 'POST',
      body: {
        'latitude': data.location.lat,
        'longitude': data.location.lon,
        'radius': 10000
      }
    })
  }

  searchChefs (query) {
    return this.request({
      url: 'https://api.donalolafood.com/api/chef/by/name',
      method: 'POST',
      body: {
        'name': query
      }
    })
  }

  getMyInfo () {
    return this.request({
      url: 'https://api.donalolafood.com/api/customer/me',
      method: 'get'
    })
  }
  setMyInfo(data) {
    return this.request({
      url: 'https://api.donalolafood.com/api/customer/save',
      method: 'post',
      body: data
    })
  }
}

export default new MapService()
